/**
 * Retrieves the translation of text.
 *
 * @see [https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/](https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/)
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 *
 * @see [https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops](https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops)
 */
import {
    useBlockProps,
    RichText,
    MediaUpload,
    MediaUploadCheck,
} from '@wordpress/block-editor';

import { Button } from '@wordpress/components';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 *
 * @see [https://www.npmjs.com/package/@wordpress/scripts#using-css](https://www.npmjs.com/package/@wordpress/scripts#using-css)
 */
import './editor.scss';

/**
 * Edit component
 *
 * @param {Object}   props
 * @param {Object}   props.attributes
 * @param {Function} props.setAttributes
 * @return {Element}
 */
export default function Edit({ attributes, setAttributes }) {
    const {
        heading, content,
        counter1_number, counter1_prefix, counter1_suffix, counter1_label,
        counter2_number, counter2_prefix, counter2_suffix, counter2_label,
        counter3_number, counter3_prefix, counter3_suffix, counter3_label,
        counter4_number, counter4_prefix, counter4_suffix, counter4_label,
        imageTop, imageBottom, logoImage,
    } = attributes;

    const updateNumber = (value) => value.replace(/[^0-9]/g, '');

    return (
        <div {...useBlockProps()}>
            <div className="hero-layout">
                {/* Logo */}
                <div className="hero-logo">
                    <MediaUploadCheck>
                        <MediaUpload
                            onSelect={(img) => setAttributes({ logoImage: img.url })}
                            allowedTypes={['image']}
                            render={({ open }) => (
                                <Button onClick={open} isSecondary>
                                    {logoImage ? 'Change Logo' : 'Upload Logo'}
                                </Button>
                            )}
                        />
                    </MediaUploadCheck>
                    {logoImage && <img src={logoImage} alt="Logo" />}
                </div>

                {/* Top media */}
                <div className="hero-image-top">
                    <MediaUploadCheck>
                        <MediaUpload
                            onSelect={(img) => setAttributes({ imageTop: img.url })}
                            allowedTypes={['image']}
                            render={({ open }) => (
                                <Button onClick={open} isSecondary>
                                    {imageTop ? 'Change Image' : 'Upload Top Image'}
                                </Button>
                            )}
                        />
                    </MediaUploadCheck>
                    {imageTop && <img src={imageTop} alt="Top Media" />}
                </div>

                {/* Bottom media */}
                <div className="hero-image-bottom">
                    <MediaUploadCheck>
                        <MediaUpload
                            onSelect={(img) => setAttributes({ imageBottom: img.url })}
                            allowedTypes={['image']}
                            render={({ open }) => (
                                <Button onClick={open} isSecondary>
                                    {imageBottom ? 'Change Image' : 'Upload Bottom Image'}
                                </Button>
                            )}
                        />
                    </MediaUploadCheck>
                    {imageBottom && <img src={imageBottom} alt="Bottom Media" />}
                </div>

                {/* Hero content */}
                <div className="hero-content">
                    <RichText
                        tagName="h1"
                        value={heading}
                        onChange={(value) => setAttributes({ heading: value })}
                        placeholder={__('Hero heading…', 'hero')}
                        className="hero-heading"
                    />
                    <RichText
                        tagName="p"
                        value={content}
                        onChange={(value) => setAttributes({ content: value })}
                        placeholder={__('Hero content…', 'hero')}
                        className="hero-description"
                    />

                    {/* Fixed Counters with Prefix/Suffix */}
                    <div className="hero-counters">
                        <div className="counter-item">
                            <div className="counter-number">
                                <RichText
                                    tagName="span"
                                    value={counter1_prefix}
                                    onChange={(value) => setAttributes({ counter1_prefix: value })}
                                    placeholder="+"
                                    className="counter-prefix"
                                />
                                <RichText
                                    tagName="strong"
                                    value={counter1_number}
                                    onChange={(value) => setAttributes({ counter1_number: updateNumber(value) })}
                                    placeholder="100"
                                    className="counter-value"
                                />
                                <RichText
                                    tagName="span"
                                    value={counter1_suffix}
                                    onChange={(value) => setAttributes({ counter1_suffix: value })}
                                    placeholder="+"
                                    className="counter-suffix"
                                />
                            </div>
                            <RichText
                                tagName="p"
                                value={counter1_label}
                                onChange={(value) => setAttributes({ counter1_label: value })}
                                placeholder="Counter label"
                                className="counter-label"
                            />
                        </div>

                        <div className="counter-item">
                            <div className="counter-number">
                                <RichText
                                    tagName="span"
                                    value={counter2_prefix}
                                    onChange={(value) => setAttributes({ counter2_prefix: value })}
                                    placeholder="+"
                                    className="counter-prefix"
                                />
                                <RichText
                                    tagName="strong"
                                    value={counter2_number}
                                    onChange={(value) => setAttributes({ counter2_number: updateNumber(value) })}
                                    placeholder="50"
                                    className="counter-value"
                                />
                                <RichText
                                    tagName="span"
                                    value={counter2_suffix}
                                    onChange={(value) => setAttributes({ counter2_suffix: value })}
                                    placeholder="+"
                                    className="counter-suffix"
                                />
                            </div>
                            <RichText
                                tagName="p"
                                value={counter2_label}
                                onChange={(value) => setAttributes({ counter2_label: value })}
                                placeholder="Counter label"
                                className="counter-label"
                            />
                        </div>

                        <div className="counter-item">
                            <div className="counter-number">
                                <RichText
                                    tagName="span"
                                    value={counter3_prefix}
                                    onChange={(value) => setAttributes({ counter3_prefix: value })}
                                    placeholder="+"
                                    className="counter-prefix"
                                />
                                <RichText
                                    tagName="strong"
                                    value={counter3_number}
                                    onChange={(value) => setAttributes({ counter3_number: updateNumber(value) })}
                                    placeholder="0"
                                    className="counter-value"
                                />
                                <RichText
                                    tagName="span"
                                    value={counter3_suffix}
                                    onChange={(value) => setAttributes({ counter3_suffix: value })}
                                    placeholder="+"
                                    className="counter-suffix"
                                />
                            </div>
                            <RichText
                                tagName="p"
                                value={counter3_label}
                                onChange={(value) => setAttributes({ counter3_label: value })}
                                placeholder="Counter label"
                                className="counter-label"
                            />
                        </div>

                        <div className="counter-item">
                            <div className="counter-number">
                                <RichText
                                    tagName="span"
                                    value={counter4_prefix}
                                    onChange={(value) => setAttributes({ counter4_prefix: value })}
                                    placeholder="+"
                                    className="counter-prefix"
                                />
                                <RichText
                                    tagName="strong"
                                    value={counter4_number}
                                    onChange={(value) => setAttributes({ counter4_number: updateNumber(value) })}
                                    placeholder="0"
                                    className="counter-value"
                                />
                                <RichText
                                    tagName="span"
                                    value={counter4_suffix}
                                    onChange={(value) => setAttributes({ counter4_suffix: value })}
                                    placeholder="+"
                                    className="counter-suffix"
                                />
                            </div>
                            <RichText
                                tagName="p"
                                value={counter4_label}
                                onChange={(value) => setAttributes({ counter4_label: value })}
                                placeholder="Counter label"
                                className="counter-label"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
