/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see [https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops](https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops)
 */
import { useBlockProps, RichText } from '@wordpress/block-editor';

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see [https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#save](https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#save)
 *
 * @param {Object} props            Block properties
 * @param {Object} props.attributes Block attributes
 * @return {Element} Element to render.
 */
export default function Save({ attributes }) {
    const {
        heading, content,
        counter1_number, counter1_prefix, counter1_suffix, counter1_label,
        counter2_number, counter2_prefix, counter2_suffix, counter2_label,
        counter3_number, counter3_prefix, counter3_suffix, counter3_label,
        counter4_number, counter4_prefix, counter4_suffix, counter4_label,
        imageTop, imageBottom, logoImage
    } = attributes;

    const renderCounter = (number, prefix, suffix, label) => (
        (number || prefix || suffix || label) && (
            <div className="counter-item">
                <div className="counter-number" data-counter={number}>
                    {prefix && <span className="counter-prefix">{prefix}</span>}
                    {number && <strong className="counter-value">{number}</strong>}
                    {suffix && <span className="counter-suffix">{suffix}</span>}
                </div>
                {label && (
                    <RichText.Content
                        tagName="p"
                        value={label}
                        className="counter-label"
                    />
                )}
            </div>
        )
    );

    return (
        <div {...useBlockProps.save()}>
            <div className="hero-layout">
                {logoImage && (
                    <div className="hero-logo">
                        <img src={logoImage} alt="Logo" />
                    </div>
                )}
                {imageTop && (
                    <div className="hero-image-top">
                        <img src={imageTop} alt="Top Media" />
                    </div>
                )}
                {imageBottom && (
                    <div className="hero-image-bottom">
                        <img src={imageBottom} alt="Bottom Media" />
                    </div>
                )}
                <div className="hero-content">
                    {heading && (
                        <RichText.Content
                            tagName="h1"
                            value={heading}
                            className="hero-heading"
                        />
                    )}
                    {content && (
                        <RichText.Content
                            tagName="p"
                            value={content}
                            className="hero-description"
                        />
                    )}
                    <div className="hero-counters">
                        {renderCounter(counter1_number, counter1_prefix, counter1_suffix, counter1_label)}
                        {renderCounter(counter2_number, counter2_prefix, counter2_suffix, counter2_label)}
                        {renderCounter(counter3_number, counter3_prefix, counter3_suffix, counter3_label)}
                        {renderCounter(counter4_number, counter4_prefix, counter4_suffix, counter4_label)}
                    </div>
                </div>
            </div>
        </div>
    );
}
